import React from "react";

class MealNav extends React.Component {
	goToLunch = () => {
		this.props.changeMenu("lunch");
	};
	goToBreakfast = () => {
		this.props.changeMenu("breakfast");
	};
	goToSnack = () => {
		this.props.changeMenu("snack");
	};
	goToDinner = () => {
		this.props.changeMenu("dinner");
	};

	newClassName = menu => {
		if (this.props.menu === menu) {
			return "active-nav";
		}
	};
	render() {
		return (
			<div className={this.props.navClasses}>
				<ul>
					<div className="nav-toggle-wrapper" onClick={this.props.toggleNav}>
						<div className="nav-toggle" />
					</div>
					<li
						onClick={() => {
							this.goToBreakfast();
							this.props.toggleNav();
						}}
						className={this.newClassName("breakfast")}>
						Breakfast
					</li>
					<li
						onClick={() => {
							this.goToLunch();
							this.props.toggleNav();
						}}
						className={this.newClassName("lunch")}>
						Lunch
					</li>
					<li
						onClick={() => {
							this.goToSnack();
							this.props.toggleNav();
						}}
						className={this.newClassName("snack")}>
						Snack
					</li>
					<li
						onClick={() => {
							this.goToDinner();
							this.props.toggleNav();
						}}
						className={this.newClassName("dinner")}>
						Dinner
					</li>
				</ul>
				<div className="nav-shadow-hide" />
			</div>
		);
	}
}

export default MealNav;



