import React from "react";
import MealNav from "./MealNav";
import BreakfastList from "./BreakfastList";
import LunchList from "./LunchList";
import SnackList from "./SnackList";
import DinnerList from "./DinnerList";

class Meals extends React.Component {
	render() {
		return (
			<div className="meals-container">
				<MealNav
					changeMenu={this.props.changeMenu}
					menu={this.props.menu}
					toggleNav={this.props.toggleNav}
					navClasses={this.props.navClasses}
				/>
				{this.props.menu === "lunch" ? (
					<LunchList
						foods={this.props.foods.lunchFoods}
						chosen={this.props.chosen.lunch}
						addToChosen={this.props.addToChosen}
						menuScroll={this.props.menuScroll}
						menuScrollDown={this.props.menuScrollDown}
					/>
				) : (
					""
				)}
				{this.props.menu === "breakfast" ? (
					<BreakfastList
						foods={this.props.foods.breakfastFoods}
						chosen={this.props.chosen.breakfast}
						addToChosen={this.props.addToChosen}
						menuScroll={this.props.menuScroll}
						menuScrollDown={this.props.menuScrollDown}
					/>
				) : (
					""
				)}
				{this.props.menu === "snack" ? (
					<SnackList
						foods={this.props.foods.snackFoods}
						chosen={this.props.chosen.snack}
						addToChosen={this.props.addToChosen}
						menuScroll={this.props.menuScroll}
						menuScrollDown={this.props.menuScrollDown}
					/>
				) : (
					""
				)}
				{this.props.menu === "dinner" ? (
					<DinnerList
						foods={this.props.foods.dinnerFoods}
						chosen={this.props.chosen.dinner}
						addToChosen={this.props.addToChosen}
						menuScroll={this.props.menuScroll}
						menuScrollDown={this.props.menuScrollDown}
					/>
				) : (
					""
				)}
			</div>
		);
	}
}

export default Meals;



