import React from "react";
import ChosenItem from "./ChosenItem";

class BreakfastNutri extends React.Component {
	render() {
		return (
			<div className="chosenlist-container">
				<ul>
					{Object.keys(this.props.chosen).map(
						key =>
							this.props.chosen[key] === 1 ? (
								<ChosenItem
									index={key}
									details={this.props.foods[key]}
									addToChosen={this.props.addToChosen}
									meal="breakfast"
									key={key}
								/>
							) : (
								""
							)
					)}
				</ul>
			</div>
		);
	}
}

export default BreakfastNutri;



