import React from "react";

import { foods } from "../foods";
// import Header from "./Header";
import Nutrition from "./Nutrition";
import Meals from "./Meals";
import Footer from "./Footer";
import { menuScroll, menuScrollDown, getPercentage } from "../helpers";

class App extends React.Component {
	state = {
		menu: "breakfast",
		foods: {},
		chosen: {
			breakfast: {},
			lunch: {},
			snack: {},
			dinner: {}
		},
		welcomeClasses: ["welcome-container"],
		navClasses: ["mealnav-container", "nav-closed"]
	};

	componentWillMount() {
		this.setState({ foods });
		this.renderWelcome();
	}

	welcomeDisappear = () => {
		let welcomeClasses = this.state.welcomeClasses;
		welcomeClasses.push("disappear");
		this.setState({
			welcomeClasses: welcomeClasses
		});
	};

	toggleNav = () => {
		if (this.state.navClasses.length === 2) {
			let navClasses = this.state.navClasses;
			navClasses.pop();
			this.setState({
				navClasses: navClasses
			});
			console.log(this.state.navClasses);
			return;
		} else {
			let navClasses = this.state.navClasses;
			navClasses.push("nav-closed");
			this.setState({
				navClasses: navClasses
			});
			console.log(this.state.navClasses.length);
		}
	};

	renderWelcome = () => {
		return (
			<React.Fragment>
				<div className={this.state.welcomeClasses.join(" ")}>
					<div className="close-block" onClick={this.welcomeDisappear} />

					<div className="welcome-wrapper">
						<div className="welcome-close" onClick={this.welcomeDisappear}>
							x
						</div>
						<h1>Welcome to the carb counter!</h1>
						<p>
							Carbs can be stealthy. Click on the foods below and watch the
							carb-meter move in real time. Try your hand at building a
							high-fat, low-carb day, from breakfast to dinner, and see what you
							learn along the way.
						</p>
						<h2>Good Luck!</h2>
					</div>
				</div>
			</React.Fragment>
		);
	};

	changeMenu = newMenu => {
		this.setState({
			menu: newMenu
		});
	};

	loadFood = () => {
		this.setState({ foods });
	};

	addToChosen = (meal, key) => {
		const chosen = { ...this.state.chosen };
		const chosenLength = Object.keys(chosen[meal]).length;
		if (chosen[meal][key] === 1) {
			delete chosen[meal][key];
			this.setState({ chosen });
		} else if (chosenLength < 6) {
			chosen[meal][key] = 1;
			this.setState({ chosen });
		}
	};
	clearChosen = () => {
		const chosen = { ...this.state.chosen };
		this.setState({chosen: {
			breakfast: {},
			lunch: {},
			snack: {},
			dinner: {}
		}
	})
	}

	render() {
		const navigation = [
			["products", "https://shop.ketologic.com/collections/ketologic"],
			["the keto 30", "https://ketologic.com/keto-30"]
		];
		return (
			<div className="carb-counter">
				<div className="carb-container">
					{/* <div className="header-container">
						<Header title="Ketologic" navigation={navigation} />
					</div> */}
					{this.renderWelcome()}

					<div className="app-container">
						<Meals
							changeMenu={this.changeMenu}
							menu={this.state.menu}
							foods={this.state.foods}
							chosen={this.state.chosen}
							addToChosen={this.addToChosen}
							menuScroll={menuScroll}
							menuScrollDown={menuScrollDown}
							toggleNav={this.toggleNav}
							navClasses={this.state.navClasses.join(" ")}
						/>
						<Nutrition
							menu={this.state.menu}
							foods={this.state.foods}
							chosen={this.state.chosen}
							getPercentage={getPercentage}
							addToChosen={this.addToChosen}
						/>
					</div>
					<div className="clear-all" onClick={this.clearChosen}>Clear all</div>
				</div>
				<Footer navigation={navigation} />
			</div>
		);
	}
}

export default App;



