import React from "react";

class FoodItem extends React.Component {
	handleClick = () => {
		this.props.addToChosen(this.props.meal, this.props.index);
	};
	renderImage = () => {
		const { details, index, chosen } = this.props;
		if (chosen[index] === 1) {
			return (
				<img
					className="food-selected food-list-image"
					src={details.image}
					alt=""
				/>
			);
		} else {
			return <img className="food-list-image" src={details.image} alt="" />;
		}
	};

	render() {
		const { details } = this.props;
		return (
			<li
				className="food-item"
				onClick={() => {
					this.handleClick();
					this.renderImage();
				}}>
				{this.renderImage()}
				<p className="food-name">{details.name}</p>
			</li>
		);
	}
}

export default FoodItem;



