import React from "react";
import BreakfastNutri from "./BreakfastNutri";
import LunchNutri from "./LunchNutri";
import SnackNutri from "./SnackNutri";
import DinnerNutri from "./DinnerNutri";
import CarbGauge from "./CarbGauge";
import MacroChart from "./MacroChart";

class Nutrition extends React.Component {
	chosenSwitch = meal => {
		switch (meal) {
			case "breakfast":
				return (
					<BreakfastNutri
						foods={this.props.foods.breakfastFoods}
						chosen={this.props.chosen.breakfast}
						addToChosen={this.props.addToChosen}
					/>
				);
			case "lunch":
				return (
					<LunchNutri
						foods={this.props.foods.lunchFoods}
						chosen={this.props.chosen.lunch}
						addToChosen={this.props.addToChosen}
					/>
				);
			case "snack":
				return (
					<SnackNutri
						chosen={this.props.chosen.snack}
						foods={this.props.foods.snackFoods}
						addToChosen={this.props.addToChosen}
					/>
				);
			case "dinner":
				return (
					<DinnerNutri
						chosen={this.props.chosen.dinner}
						foods={this.props.foods.dinnerFoods}
						addToChosen={this.props.addToChosen}
					/>
				);
			default:
				return "";
		}
	};
	render() {
		return (
			<div className="nutrition-container">
				<div className="chosen-foods-container">
					<div className="nutrition-header">
						<h2>Nutritional Info:</h2>
					</div>
					{this.chosenSwitch(this.props.menu)}
				</div>
				<div className="nutrition-stats-container">
					<CarbGauge
						menu={this.props.menu}
						chosen={this.props.chosen}
						foods={this.props.foods}
					/>
					<MacroChart
						menu={this.props.menu}
						chosen={this.props.chosen}
						foods={this.props.foods}
						getPercentage={this.props.getPercentage}
					/>
				</div>
			</div>
		);
	}
}

export default Nutrition;



