import React from "react";

class MacroChart extends React.Component {
	renderMacrosSimple = (menu, menuFoods) => {
		if (
			menu === "breakfast" ||
			menu === "lunch" ||
			menu === "snack" ||
			menu === "dinner"
		) {
			let carbs = 0;
			let protein = 0;
			let fat = 0;
			let fatGraphClasses = ["graph-container"];
			let fatBarClasses = ["bar-wrapper"];
			let proteinGraphClasses = ["graph-container"];
			let proteinBarClasses = ["bar-wrapper"];
			let carbsGraphClasses = ["graph-container"];
			let carbsBarClasses = ["bar-wrapper"];

			// 		let renderCarbs = key => {
			// 			if (this.props.chosen[menu][key] === 1) {
			// 				carbs = carbs + this.props.foods[menuFoods][key].carbs;
			// 				// carbs + this.props.foods.lunchFoods[key].carbs +
			// 				// carbs + this.props.foods.snackFoods[key].carbs +
			// 				// carbs + this.props.foods.dinnerFoods[key].carbs;
			// 				protein = protein + this.props.foods[menuFoods][key].protein;
			// 				fat = fat + this.props.foods[menuFoods][key].fat;
			// 			}

			let renderTotalMacros = () => {
				if (Object.keys(this.props.chosen.breakfast).length) {
					Object.keys(this.props.chosen.breakfast).map(key => {
						carbs = carbs + this.props.foods.breakfastFoods[key].carbs;
						protein = protein + this.props.foods.breakfastFoods[key].protein;
						fat = fat + this.props.foods.breakfastFoods[key].fat;
						return carbs;
					});
				}
				if (Object.keys(this.props.chosen.lunch).length) {
					Object.keys(this.props.chosen.lunch).map(key => {
						carbs = carbs + this.props.foods.lunchFoods[key].carbs;
						protein = protein + this.props.foods.lunchFoods[key].protein;
						fat = fat + this.props.foods.lunchFoods[key].fat;
						return carbs;
					});
				}
				if (Object.keys(this.props.chosen.snack).length) {
					Object.keys(this.props.chosen.snack).map(key => {
						carbs = carbs + this.props.foods.snackFoods[key].carbs;
						protein = protein + this.props.foods.snackFoods[key].protein;
						fat = fat + this.props.foods.snackFoods[key].fat;
						return carbs;
					});
				}
				if (Object.keys(this.props.chosen.dinner).length) {
					Object.keys(this.props.chosen.dinner).map(key => {
						carbs = carbs + this.props.foods.dinnerFoods[key].carbs;
						protein = protein + this.props.foods.dinnerFoods[key].protein;
						fat = fat + this.props.foods.dinnerFoods[key].fat;
						return carbs;
					});
				}
				if (this.props.getPercentage(fat, 160) > 33) {
					fatGraphClasses.push("graph-yellow");
					fatBarClasses.push("graph-yellow");
				}
				if (this.props.getPercentage(protein, 100) > 33) {
					proteinGraphClasses.push("graph-yellow");
					proteinBarClasses.push("graph-yellow");
				}
				if (this.props.getPercentage(carbs, 25) > 33) {
					carbsGraphClasses.push("graph-yellow");
					carbsBarClasses.push("graph-yellow");
				}
				if (this.props.getPercentage(fat, 160) > 66) {
					fatGraphClasses.push("graph-red");
					fatBarClasses.push("graph-red");
				}
				if (this.props.getPercentage(protein, 100) > 66) {
					proteinGraphClasses.push("graph-red");
					proteinBarClasses.push("graph-red");
				}
				if (this.props.getPercentage(carbs, 25) > 66) {
					carbsGraphClasses.push("graph-red");
					carbsBarClasses.push("graph-red");
				}
			};

			return (
				<div className="macro-bars-container">
					<div className="macro-numbers">
						{/* {Object.keys(this.props.chosen[menu]).forEach(key => {
							renderCarbs(key);
						})} */}
						{renderTotalMacros()}
						<p>{this.props.getPercentage(fat, 160)}%</p>
						<p>{this.props.getPercentage(protein, 100)}%</p>
						<p>{this.props.getPercentage(carbs, 25)}%</p>
					</div>
					<div className="macro-graphs-container">
						<div className="macro-label-wrapper">
							<div className={fatGraphClasses.join(" ")}>
								<div
									className={fatBarClasses.join(" ")}
									style={
										this.props.getPercentage(fat, 160) > 0
											? {
													height:
														"calc(" +
														this.props.getPercentage(fat, 160) +
														"% + 10px)"
												}
											: {}
									}
								/>
							</div>
							<p>Fat</p>
						</div>

						<div className="macro-label-wrapper">
							<div className={proteinGraphClasses.join(" ")}>
								<div
									className={proteinBarClasses.join(" ")}
									style={
										this.props.getPercentage(protein, 160) > 0
											? {
													height:
														"calc(" +
														this.props.getPercentage(protein, 100) +
														"% + 10px)"
												}
											: {}
									}
								/>
							</div>
							<p>Protein</p>
						</div>

						<div className="macro-label-wrapper">
							<div className={carbsGraphClasses.join(" ")}>
								<div
									className={carbsBarClasses.join(" ")}
									style={
										this.props.getPercentage(carbs, 160) > 0
											? {
													height:
														"calc(" +
														this.props.getPercentage(carbs, 25) +
														"% + 10px)"
												}
											: {}
									}
								/>
							</div>
							<p>Carbs</p>
						</div>
					</div>
				</div>
			);
		} else {
			return (
				<div className="macro-bars-container">
					<div className="macro-numbers">
						<p>0%</p>
						<p>0%</p>
						<p>0%</p>
					</div>
					<div className="macro-graphs-container">
						<div className="macro-label-wrapper">
							<div className="graph-container">
								<div
									className="bar-wrapper"
									style={{
										height: "0%"
									}}
								/>
							</div>
							<p>Fat</p>
						</div>

						<div className="macro-label-wrapper">
							<div className="graph-container">
								<div
									className="bar-wrapper"
									style={{
										height: "0%"
									}}
								/>
							</div>
							<p>Protein</p>
						</div>

						<div className="macro-label-wrapper">
							<div className="graph-container">
								<div
									className="bar-wrapper"
									style={{
										height: "0%"
									}}
								/>
							</div>
							<p>Carbs</p>
						</div>
					</div>
				</div>
			);
		}
	};
	render() {
		const menu = this.props.menu;
		const menuFoods = menu + "Foods";
		return (
			<div className="macro-chart-container">
				<div className="macro-measurement-container">
					<div>175 g</div>
					<div>150 g</div>
					<div>125 g</div>
					<div>100 g</div>
					<div>75 g</div>
					<div>50 g</div>
					<div>25 g</div>
					<div>0 g</div>
				</div>

				{this.renderMacrosSimple(menu, menuFoods)}
			</div>
		);
	}
}

export default MacroChart;



