export function menuScroll() {
	let element = document.querySelector(".foodlist-container ul"),
		style = window.getComputedStyle(element),
		top = style.getPropertyValue("top"),
		height = parseInt(style.getPropertyValue("height"), 10),
		vLimit = (height - 510) * -1,
		topInt = parseInt(top, 10),
		newTopInt = topInt - 170;
	if (newTopInt >= vLimit) {
		element.style.top = newTopInt + "px";
	} else if (height >= 510) {
		element.style.top = vLimit + "px";
	}
}
export function menuScrollDown() {
	var element = document.querySelector(".foodlist-container ul"),
		style = window.getComputedStyle(element),
		top = style.getPropertyValue("top"),
		topInt = parseInt(top, 10),
		newTopInt = topInt + 170;
	if (newTopInt <= 0) {
		element.style.top = newTopInt + "px";
	} else {
		element.style.top = 0 + "px";
	}
}

export function getPercentage(total, max) {
	let percentage = Math.floor(total / max * 100);
	return percentage;
}

export function rando(arr) {
	return arr[Math.floor(Math.random() * arr.length)];
}
