import React from "react";

class Chosen extends React.Component {
	handleClick = () => {
		this.props.addToChosen(this.props.meal, this.props.index);
	};
	render() {
		const { details } = this.props;
		return (
			<li
				key={this.props.index}
				className="food-item chosen-item"
				onClick={this.handleClick}>
				<img className="chosen-list-image" src={details.image} alt="" />
				<div className="chosen-macro-container">
					<h3>{details.name}</h3>
					<ul>
						<li>fat: {details.fat}g</li>
						<li>protein: {details.protein}g</li>
						<li>carbs: {details.carbs}g</li>
					</ul>
				</div>
			</li>
		);
	}
}

export default Chosen;



