import React from "react";

class Footer extends React.Component {
	render() {
		return (
			<footer className="footer">
				<p>powered by:</p>
				<a href="https://ketologic.com/" target="_blank">
					<img alt="" className="footer-logo" src="images/new-logo.svg" />
				</a>
				<ul>
					{this.props.navigation.map((navLink, index) => {
						return (
							<li key={index}>
								<a href={navLink[1]} target="_none">
									{navLink[0]}
								</a>
							</li>
						);
					})}
				</ul>
			</footer>
		);
	}
}

export default Footer;



