import React from "react";
import { getGoodCopy, getMediumCopy, getHighCopy } from "../helpers";

class CarbGauge extends React.Component {
	renderCarbs = (menu, menuFoods) => {
		if (
			menu === "breakfast" ||
			menu === "lunch" ||
			menu === "snack" ||
			menu === "dinner"
		) {
			let carbs = 0;
			// Object.keys(this.props.chosen[menu]).forEach(key => {
			// 	if (this.props.chosen[menu][key] === 1) {
			// 		carbs = carbs + this.props.foods[menuFoods][key].carbs;
			// 	}
			// });
			if (Object.keys(this.props.chosen.breakfast).length) {
				Object.keys(this.props.chosen.breakfast).map(key => {
					carbs = carbs + this.props.foods.breakfastFoods[key].carbs;
					return carbs;
				});
			}
			if (Object.keys(this.props.chosen.lunch).length) {
				Object.keys(this.props.chosen.lunch).map(key => {
					carbs = carbs + this.props.foods.lunchFoods[key].carbs;
					return carbs;
				});
			}
			if (Object.keys(this.props.chosen.snack).length) {
				Object.keys(this.props.chosen.snack).map(key => {
					carbs = carbs + this.props.foods.snackFoods[key].carbs;
					return carbs;
				});
			}
			if (Object.keys(this.props.chosen.dinner).length) {
				Object.keys(this.props.chosen.dinner).map(key => {
					carbs = carbs + this.props.foods.dinnerFoods[key].carbs;
					return carbs;
				});
			}
			return parseInt(carbs, 10);
		} else {
			return <React.Fragment>0</React.Fragment>;
		}
	};
	gaugeAnimation = carbs => {
		if (carbs >= 21) {
			return "carb-total hard-shake";
		} else {
			return "carb-total";
		}
	};

	renderGauge = (menu, menuFoods) => {};
	renderRandom = carbs => {
		if (carbs > 0 && carbs < 13) {
			return <div className="random1">{getGoodCopy()}</div>;
		} else if (carbs >= 13 && carbs < 21) {
			return <div className="random2">{getMediumCopy()}</div>;
		} else if (carbs >= 21) {
			return <div className="random3 little-shake">{getHighCopy()}</div>;
		}
	};

	render() {
		const menu = this.props.menu;
		const menuFoods = menu + "Foods";
		return (
			<div className="carb-gauge-container">
				<div className="random-copy-container">
					{this.renderRandom(this.renderCarbs(menu, menuFoods))}
				</div>
				<div className={this.gaugeAnimation(this.renderCarbs(menu, menuFoods))}>
					<svg
						id="gauge-parts"
						xmlns="http://www.w3.org/2000/svg"
						viewBox="0 0 171.04 125.1">
						<title>carb-gauge</title>
						<path
							id="gauge-8"
							style={
								this.renderCarbs(menu, menuFoods) > 24
									? { fill: "#8c103d" }
									: {}
							}
							d="M155.79,123.32c4.43,2.62,5.16,2.6,7.35-1.82a76.71,76.71,0,0,0,7.72-28.16c.26-3.29-.59-4-4-4.29-1.15-.08-2.32,0-3.49,0-2.58.06-3.76,1.27-4,4a70.1,70.1,0,0,1-6.82,23.79C151,119.88,151.64,120.87,155.79,123.32Z"
						/>
						<path
							id="gauge-7"
							style={
								this.renderCarbs(menu, menuFoods) > 21
									? { fill: "#a94c6e" }
									: {}
							}
							d="M165.58,83c5.14,0,5.76-.41,5.37-5.33a77,77,0,0,0-7.8-28.14c-1.47-3-2.59-3.18-5.62-1.64-1,.52-2,1.18-3,1.8-2.19,1.38-2.58,3-1.44,5.49a70.32,70.32,0,0,1,6.34,23.92C159.71,82.46,160.75,83,165.58,83Z"
						/>
						<path
							id="gauge-6"
							style={
								this.renderCarbs(menu, menuFoods) > 17.5
									? { fill: "#ffca59" }
									: {}
							}
							d="M128.25,17.93c2.62-4.42,3.27-4.76,7.31-1.93a76.82,76.82,0,0,1,20.31,21c1.81,2.76,1.43,3.84-1.42,5.68-1,.62-2,1.12-3.07,1.66-2.3,1.19-3.92.69-5.47-1.54A70.08,70.08,0,0,0,128.5,25.2C125.71,23.24,125.79,22.08,128.25,17.93Z"
						/>
						<path
							id="gauge-5"
							style={
								this.renderCarbs(menu, menuFoods) > 14
									? { fill: "#ffd783" }
									: {}
							}
							d="M94,.3a82.56,82.56,0,0,1,28,7.85c3.15,1.56,3.29,2.64,1.48,5.88-.48.86-1.06,1.68-1.49,2.57-1.16,2.41-2.94,2.95-5.31,1.88A79.3,79.3,0,0,0,92,11.81a4.45,4.45,0,0,1-3.11-2.63,14.73,14.73,0,0,1,0-6.34C89.36.12,91.75.12,94,.3Z"
						/>
						<path
							id="gauge-4"
							style={
								this.renderCarbs(menu, menuFoods) > 10.5
									? { fill: "#ffe4ac" }
									: {}
							}
							d="M48.49,14.88c-2.59-4.44-2.57-5.18,1.87-7.33A76.75,76.75,0,0,1,78.57,0c3.3-.24,4.05.61,4.27,4,.07,1.16,0,2.32,0,3.49-.07,2.58-1.29,3.75-4,4A70.28,70.28,0,0,0,55,18.2C51.89,19.69,50.91,19.05,48.49,14.88Z"
						/>
						<path
							id="gauge-3"
							style={
								this.renderCarbs(menu, menuFoods) > 7 ? { fill: "#6bdabc" } : {}
							}
							d="M18,43.24C13.57,40.66,13.22,40,16,36A76.84,76.84,0,0,1,36.76,15.41c2.74-1.84,3.82-1.47,5.69,1.37.64,1,1.15,2,1.7,3,1.21,2.28.74,3.9-1.48,5.48a70.3,70.3,0,0,0-17.4,17.6C23.35,45.72,22.19,45.66,18,43.24Z"
						/>
						<path
							id="gauge-2"
							style={
								this.renderCarbs(menu, menuFoods) > 3.5
									? { fill: "#90e3cd" }
									: {}
							}
							d="M5.71,83C.57,82.94,0,82.56.34,77.64A76.63,76.63,0,0,1,8.14,49.5c1.46-3,2.58-3.18,5.62-1.64,1,.52,2,1.18,3,1.8,2.19,1.38,2.59,3,1.45,5.49a70.1,70.1,0,0,0-6.35,23.92C11.57,82.46,10.53,83,5.71,83Z"
						/>
						<path
							id="gauge-1"
							style={
								this.renderCarbs(menu, menuFoods) > 0 ? { fill: "#b6edde" } : {}
							}
							d="M15.11,123.32c-4.42,2.62-5.16,2.6-7.34-1.82A76.72,76.72,0,0,1,0,93.34c-.26-3.29.59-4,4-4.29,1.16-.08,2.33,0,3.49,0,2.59.06,3.77,1.27,4.06,4a70.31,70.31,0,0,0,6.81,23.79C19.89,119.88,19.26,120.87,15.11,123.32Z"
						/>
					</svg>
					<div className="carbs-display">
						<span>{this.renderCarbs(menu, menuFoods)}</span> g
						<p>carbs</p>
					</div>
				</div>
			</div>
		);
	}
}

export default CarbGauge;



