import React from "react";
import FoodItem from "./FoodItem";

class DinnerList extends React.Component {
	handleClick = () => {
		this.props.menuScroll();
	};
	handleClickDown = () => {
		this.props.menuScrollDown();
	};
	render() {
		return (
			<React.Fragment>
				<div className="list-up-button" onClick={this.handleClickDown} />
				<div className="foodlist-container">
					<ul>
						{Object.keys(this.props.foods).map(key => (
							<FoodItem
								key={key}
								index={key}
								details={this.props.foods[key]}
								chosen={this.props.chosen}
								addToChosen={this.props.addToChosen}
								meal="dinner"
							/>
						))}
					</ul>
				</div>
				<div className="list-down-button" onClick={this.handleClick} />
			</React.Fragment>
		);
	}
}

export default DinnerList;



